@import url("https://fonts.googleapis.com/css2?family=Chango&family=Short+Stack&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Short Stack";
}

html {
  font-size: 62.5%;
}

body {
  font-size: 2.4rem;
  color: #5a3e2b;
  font-weight: 500;
}

.app {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

h1 {
  text-align: center;
  background-color: #e0eec6;
  font-family: "Chango";
  font-size: 6rem;
  text-transform: uppercase;
  font-weight: 200;
  word-spacing: 30px;
  letter-spacing: 0;
  padding: 2.4rem 0;
}

.add-form {
  background-color: #7ca982;
  padding: 2.8rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}

h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

button,
select,
input {
  background-color: #e0eec6;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  font-size: 1.8rem;
  cursor: pointer;
}

#check {
  margin-right: 5px;
}

#delete {
  padding: 4px 8px 8px 8px;
}

.add-form button {
  text-transform: uppercase;
  background-color: #e0eec6;
}

.list {
  background-color: #243e36;
  color: #e0eec6;
  padding: 4rem 0;

  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}

.actions button,
.list select {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
}

.list ul {
  list-style: none;
  width: 80%;
  overflow: hidden;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: start;
}

.list li {
  display: flex;
  align-items: center;
}

input[type="checkbox"] {
  height: 2rem;
  width: 2rem;
  background-color: #e0eec6;
  accent-color: #e0eec6;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
  padding: 0.8rem;
  transform: translateY(2px);
}

.stats {
  background-color: #f1f7ed;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}
